import React from 'react';
import './Home.css';
import logo2 from './logo2.svg';

const Home = () => 
  <div className="page home globe-background">
    <div className="home-logo-container">
      <img src={logo2} alt="Sequel Music Group"/>
    </div>
  </div>;

export default Home;


